export default {
	data() {
		return {
			user:[],
			sex:"",
			id:""
		}
	},
	methods: {
		img(){
			uni.showActionSheet({
				itemList:[
					"查看我的头像",
					"从手机相册中选中",
				],
				success:res=>{
					console.log(res);
					if(res.tapIndex = 1){
						uni.chooseImage({
						    count: 1, //默认9
						    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
						    sourceType: ['album'], //从相册选择
						    success:  (res)=> {
						        console.log(res);
						    }
						});
					}
				}
			})
		},
		out(){
			uni.request({
				url: `${this.$api}/user/logout?qq=122212489`,
				method: 'POST',
				data: {
					userId:this.user.id
				},
				success: res => {
					console.log(res);
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	onShow() {
		if(uni.getStorageSync("user")){
			this.user = JSON.parse(uni.getStorageSync("user"))
			// this.id = this.user.id
			// console.log(this.id);
		}
		if(this.user.sex === 1){
			this.user.sex = "男"
		}else{
			this.user.sex = "女"
		}
	}
}
